import { useState } from "react";
import { Stack, TextField } from "@mui/material";
import { useFormikContext } from "formik";
import FormTextFieldLabel from "./FormTextFieldLabel";

export default function FormTextField({ name, label, sx, children, emptyCheck = false, ...otherProps }) {
  const [blur, setBlur] = useState(true);

  const { values, touched, errors, handleChange, handleBlur, dirty } =
    useFormikContext();

  return (
    <Stack sx={{ mb: "22px", ...sx }}>
      <FormTextFieldLabel label={label} />
      <TextField
        fullWidth
        autoComplete="off"
        id={name}
        name={name}
        value={values[name] || ""}
        onChange={(e) => {
          handleChange(e);
          setBlur(false);
        }}
        onBlur={(e) => {
          handleBlur(e);
          setBlur(true);
        }}
        error={
          blur &&
          Boolean(values[name] || emptyCheck) &&
          touched[name] &&
          Boolean(errors[name])
        }
        helperText={
          blur &&
          Boolean(values[name] || emptyCheck) &&
          (touched[name] || dirty[name]) &&
          errors[name]
        }
        {...otherProps}
      >{children}</TextField>
    </Stack>
  );
}
