import { Box, Stack, Typography } from "@mui/material";
import { colors } from "../../../../../../utils/colors";
import React from "react";
import { useFormikContext } from "formik";
import FormTextField from "../../../../../../components/FormTextField";

export default function EcoCampaignDetails() {

  const { values } = useFormikContext();

  return (
    <React.Fragment>
      <FormTextField label="Name*" name="title" emptyCheck={true}  />
      <FormTextField label="Short Description (30 chars)" name="shortDescription" emptyCheck={true}   />
      <Stack sx={{ mb: "22px" }}>
        <Typography
          style={{ color: colors.blackMain }}
          className="fs-14 ff-Roboto fw-medium mb-7"
        >
          Detailed Description (500 chars)
        </Typography>
        <Box
          style={{
            padding: "16px",
            backgroundColor: "rgba(73, 96, 115, 0.11)",
            minHeight: '176px'
          }}
        >
          <Typography>
            {values?.detailedDescription}
          </Typography>
        </Box>
      </Stack>
    </React.Fragment>
  );
}
